  monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* width: 100%; */
  /* overflow: auto; */
  /* height: 100%; */
}


p{
  display: block;
}

h1,h2,h3,h4,h5,h6,p,span{
  text-align: left;
  
}

.App-header {
  background-color: #101114;
  height: 6vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.container{
  margin: 0;
  padding: 0;
  display: flex;
  height: 94vh;
  flex-direction: row;
  flex-wrap: wrap;

}
/* main>div{
  flex:1;
} */
.item{
  flex: 1 1;
}

#editor{
  background-color: #131420;
  height: 100%;
  /* margin-top: 24px; */
  /* flex: 1; */
}

#preview{
/* flex: 1; */
  background-color: #fff;
  height: 100%;
}
:root{
    text-align: center;
}

.markdown-body {
    text-align: left;
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
    line-height: 1.5;
    color: #D8D8D8;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    font-size: 16px;
    word-wrap: break-word;
}

.editor {
    width: 90%;
    height: 96%;
    display: block;
    overflow: auto;
    margin-top: 2%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 2%;
}


.editor:focus {
    outline: none;
}




  
/* width */
.editor::-webkit-scrollbar {
    width: 4px;
    height: 2px;
  }
  
  /* Track */
  .editor::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgb(37, 37, 37); 
    border-radius: 4px;
  }
   
  /* Handle */
  .editor::-webkit-scrollbar-thumb {
    background: #101114; 
    border-radius: 10px;
  }
  .editor::-webkit-scrollbar-track-piece {
    height: 4px;
    width: 4px;
}

:root{
    text-align: center;
}

.preview {
    width: 90%;
    height: 96%;
    display: block;
    overflow: auto;
    margin-top: 2%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 2%;
}




/* width */
.preview::-webkit-scrollbar {
    width: 4px;
}
  
  /* Track */
  .preview::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgb(73, 73, 73); 
    border-radius: 4px;
  }
   
  /* Handle */
  .preview::-webkit-scrollbar-thumb {
    background: #dfdfdf; 
    border-radius: 10px;
  }
  

  .preview blockquote {
    border-left: .25em solid #dfe2e5;
    color: #6a737d;
    padding: 0 1rem;
  }

  .preview pre code {
    background-color: transparent;
    border: 0;
    display: inline;
    font-size: 12px;
    overflow: visible;
    padding: 0;
    word-wrap: normal;
  }

  code{
      background-color: rgba(27,31,35,.05);
      border-radius: 4px;
      overflow: auto;
      line-height: 1.45;
      font-size: 85%;
      padding: 4px;

  }

  .preview pre {
    background: #f6f8fa;
    padding: 16px;
    border-radius: 4px;
    overflow: auto;
    line-height: 1.45;
    font-size: 85%;
    text-align: left;
  }

  .preview h1 {
    border-bottom: 1px solid #eaecef;
  }

  .preview h2 {
    border-bottom: 1px solid #eaecef;
  }

  .preview table {
      display: table;
      overflow: auto;
      width: 100%;
      margin-bottom : 16px;
      margin-top: 0;
      border-collapse: collapse;
    border-spacing: 2px;
}
  .preview table tr{
      background-color: #fff;
      border-top: 1px solid #c6cbd1;
  }

  .preview table tr:nth-child(odd){
    background-color: #f6f8fa;
  }

  .preview td,
  th {
    background-color: white;
    border-top: 1px solid #c6cbd1;
    padding-left: 5px;
    padding-right: 5px;
  }

